/* ----------------------
		Change default font
*/
body{
	font-family: 'Source Sans Pro', 'Helvetica Neue', sans-serif;
}
strong, b{
	font-weight: 600;
}
/* ----------------------
		Copy modules
		1 em = 10px (but should be 16px)
		@Note: According to styleguide
*/
h1, h2, h3, p, q{
	margin: 0;
}
h1{
	font-size: 32px;
	line-height: 120%;
	font-weight: 200;
}
h2{
	font-size: 24px;
	line-height: 130%;
	font-weight: 200;
}
h3{
	line-height: 135%;
	font-size: 20px;
}
h4{
	line-height: 150%;
	font-size: 16px;
	color: rgb(225,70,75);
	margin-bottom: 0;
}
p{
	line-height: 150%;
	font-size: 16px;
}
blockquote{
	font-size: 32px;
	line-height: 130%;
}

.prominent-headline h2{
	padding: 0;
	margin-bottom: 10px;
	font-size: 200%;
	font-weight: 400;
	text-align: left;
}
.prominent-headline--red h2{
	color: rgb(225,70,75);
}
.headline--align-left, .block-content-copy .headline--align-left{
	text-align: left;
}
.red-section .user-features .headline--align-left{
	margin-left: 20px;
}
